
import Sticky from 'vux/src/components/sticky/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'

import { setStore, getStore } from '../../config/mUtils'
import { getOrderList, doOrderStatus } from '../../service/getData'

export default {
components: {
Sticky,
XHeader,
Scroller,
LoadMore,
Tab,
TabItem,
Divider,
XButton,
Flexbox,
FlexboxItem,
Confirm
},
mounted() {
this.$vux.loading.show({
text: '...'
})


getOrderList(getStore('userId'), this.pageIndex, this.$route.query.type*1-1).then(resolved => {
if(resolved.msgCode == '0000') {
resolved.data.length === 0 ? this.isempty = true : this.isempty = false;
var screenHeight = screen.height;
document.getElementsByClassName("scroller")[0].style.height = screenHeight - 130 + "px";
this.$nextTick(() => {
this.$refs.scroller.reset()
this.$vux.loading.hide()
})
this.orderList = resolved.data
this.indexNum = this.$route.query.type*1
console.log(resolved.data)
this.$nextTick(() => {
this.$refs.scroller.reset()
})
} else {
this.$nextTick(() => {
this.$vux.loading.hide()
this.$vux.toast.show({
text: resolved.msg,
type:'cancel'
})
})
}
})
setTimeout(() => {
this.$vux.loading.hide()
}, 5000)
},
methods: {
showMenus() {},
options(name, index) {
this.pageIndex = 1
this.$nextTick(() => {
this.$vux.loading.show({
text: '...'
})
this.$refs.scroller.reset({
top: 0
})
})
this.indexNum = index
this.indexName = name
this.orderList = this.lists[index]
getOrderList(getStore('userId'), this.pageIndex, index - 1).then(resolved => {
this.orderList = resolved.data
this.$nextTick(() => {
this.$refs.scroller.reset()
this.$vux.loading.hide()
})
resolved.data.length === 0 ? this.isempty = true : this.isempty = false;
if(resolved.data.length === 0 || resolved.data.length < 10) {
this.pullupEnabled = false
return this.$refs.scroller.disablePullup()
}else{
this.$refs.scroller.enablePullup()
this.pullupEnabled = true
}
})
},
loadMore() { //
setTimeout(() => {
this.pageIndex += 1
this.$nextTick(() => {
getOrderList(getStore('userId'), this.pageIndex, this.indexNum - 1).then(resolved => {
this.orderList = this.orderList.concat(resolved.data)
this.$nextTick(() => {
this.$refs.scroller.reset()
})
if(resolved.data.length === 0 || resolved.data.length < 10) {
this.$vux.toast.show({
text: ''
})
this.pullupEnabled = false
this.$refs.scroller.disablePullup()
}
})
this.$refs.scroller.donePullup()
})
}, 2000)
},
refresh() { //
setTimeout(() => {
this.pageIndex = 1
this.$nextTick(() => {
setTimeout(() => {
getOrderList(getStore('userId'), this.pageIndex, this.indexNum - 1).then(resolved => {
if(resolved.data.length === 0) {
this.isempty = true
this.pullupEnabled = false
this.status.pullupStatus= 'disabled'
}else if(resolved.data.length < 10){
this.isempty = false
this.pullupEnabled = false
this.status.pullupStatus= 'disabled'
}else{
this.status.pullupStatus= 'default'
this.isempty = false
this.pullupEnabled = true
this.orderList = resolved.data
this.$nextTick(()=>{
this.$refs.scroller.reset()
this.$refs.scroller.enablePullup()
this.$vux.loading.hide()
})
}
})
this.$refs.scroller.donePulldown()
}, 10)
})
}, 2000)
},
openDateils(i) {
console.log(i)
this.$router.push({
name: 'OrderDateilsFinish',
query: {
id: i
}
})
},
onCancel(){
this.isdisabled = false
},
onConfirm(){
console.log(this.myid)
doOrderStatus(this.myid, 3, getStore('userId')).then(resolved => {
if(resolved.msgCode === '0000') {
this.$nextTick(()=>{
this.orderList[index].orderStatus = 3
this.$vux.toast.show({
position: 'middle',
text: ''
})
})
} else {
this.$nextTick(()=>{
this.isdisabled = false
this.$vux.toast.show({
type: 'text',
position: 'middle',
text: resolved.msg
})
})
}
})
},
payment(id, priceSum) {
this.$router.push({
name: 'PaymentListItem',
query: {
orderId: id,
priceSum: priceSum
}
})
console.log('')
},
operation(type, id, index) {
this.isdisabled = true
if(type === 'comment') {
this.$router.push({
name: 'Grade',
query: {
id: id
}
})
console.log('')
} else if(type === 'cancel') {

doOrderStatus(id, 5, getStore('userId')).then(resolved => {
if(resolved.msgCode == '0000') {
this.$nextTick(() => {
this.orderList[index].orderStatus = 5
this.$vux.toast.show({
text:'',
type:'text',
})
this.isdisabled = false
})
} else {
this.$vux.toast.show({
text:resolved.msg
})
this.isdisabled = false
}
})

} else if(type === 'delete') {

doOrderStatus(id, 6, getStore('userId')).then(resolved => {
if(resolved.msgCode == '0000') {
this.orderList.splice(index,1)
this.$nextTick(() => {
this.$refs.scroller.reset()
this.$vux.toast.show({
text:'',
type:'text',
})
this.isdisabled = false
})
} else {
this.$nextTick(() => {
this.$vux.toast.show({
text:resolved.msg,
type:'text',
})
this.isdisabled = false
})
}
})
console.log('')
} else if(type === 'again') {
this.$router.push({
name: 'Commodity',
query: {
id: id
}
});
this.isdisabled = false
console.log('')
} else if(type === 'signfor') {
this.show = true
this.myid = id
}
}
},
watch:{
idr(val){
this.myidr = val
console.log(val)
}
},
data() {
return {
title: '',
isempty: true,
show:false,
myid:0,
isdisabled:false,
indexNum: 0,
indexName: '',
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
list: ['', '', '', '', ''],
lists: [],
orderList: [],
pageIndex: 1,
index: this.$route.query.type*1,
pulldown:{
content: '',
height: 60,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '...',
clsPrefix: 'xs-plugin-pulldown-'
},
pullup:{
content: '',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '',
upContent: '',
loadingContent: '',
clsPrefix: 'xs-plugin-pullup-'
}
}
}
}



